.steven-container {
  display: flex;
  justify-content: center;
  align-items: flex-start; /* Move the container higher */
  height: 100vh; /* Reduce height to allow some space above */
  padding-top: 30px;
  opacity: 0;
  transform: translateY(100px); /* Start from below */
  transition: opacity 2s ease-out, transform 2s ease-out; /* Apply transition */
}

.steven-container.slide-up {
  opacity: 1;
  transform: translateY(0);
}

.steven-box {
  display: flex;
  align-items: center;
  background-color: #1e1e2f; /* Darker shade for a sleek appearance */
  border-radius: 20px; /* More rounded corners */
  box-shadow: 0px 10px 40px rgba(0, 0, 0, 0.3); /* Stronger shadow for depth */
  padding: 40px; /* Larger padding for spacious feel */
  max-width: 1000px; /* Wider container */
  width: 100%;
  gap: 30px; /* Increased gap between image and text */
  margin: 0 auto; /* Center horizontally */
  transition: transform 0.3s ease, box-shadow 0.3s ease; /* Smooth hover effect */
}

.steven-box:hover {
  transform: translateY(-5px);
  box-shadow: 0px 12px 40px rgba(0, 0, 0, 0.3);
}

.steven-image {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  object-fit: cover;
}

.steven-text {
  color: #f5f5f5;
  margin-left: 20px;
}

.steven-text h2 {
  font-size: 32px; /* Slightly larger to create a clearer distinction from body text */
  font-weight: 600; /* Semi-bold for a less heavy look, but still bold enough */
  margin-bottom: 15px; /* Slightly more space to separate from the content below */
  letter-spacing: 1px;
  font-family: 'Roboto', sans-serif;
}

.steven-text p {
  font-size: 20px; /* Slightly larger font size for better readability */
  line-height: 1.6; /* Adjusted line height for a balanced look */
  color: #666; /* Softer grey color to make the text less harsh on the eyes */
  margin-bottom: 20px; /* Adds space below paragraphs to separate them from others */
  text-align: justify;
  font-family: 'Roboto', sans-serif;
}


